<template>
  <div id="app">
    <div id="cnt-data-content">
      <div class="row header">
          <div class="left">
            <h2> HEA/OAHR </h2>
            <h1> XIY 国际机场 </h1>
            <h3> CHINA </h3>
          </div>
          <div class="right">
            <div class="top">
              <el-row :gutter="20">
                <el-col :span="6" style="padding: 0 0 0 10px;" class="col">
                  <div class="grid-content bg-purple">
                    <div class="cnt-chart" id="rating-overall" style="background: rgb(109, 109, 109);">
                      <div class="slice one" style="transform: rotate(90deg); background: rgb(248, 192, 35);"></div>
                      <div class="slice two" style="transform: rotate(187.2deg); background: rgb(248, 192, 35);"></div>  
                      <div class="chart-center">
                        <span style="color: rgb(248, 192, 35);">52%</span>
                      </div>
                    </div>  
                    <div class="chart-value chart-label">我的系统</div>
                    <div class="chart-value charts-label">评分 <i class="el-icon-question"></i></div>
                  </div>
                </el-col>
                <el-col :span="10" style="padding: 0;" class="col">
                  <div class="grid-content bg-purple">
                    <el-row type="flex" class="row-bg">
                      <el-col :span="8">
                        <div class="grid-content bg-purple sum">
                          <i class="el-icon-star-off"></i>
                          <div class="weather-value ng-binding">8℃</div>
                          <div class="weathers-value">WEATHER</div>
                        </div>
                      </el-col>
                      <el-col :span="8">
                        <div class="grid-content bg-purple sum">
                          <i class="el-icon-sort"></i>
                          <div class="weather-value ng-binding">8℃</div>
                          <div class="weathers-value">WEATHER</div>
                        </div>
                      </el-col>
                      <el-col :span="8">
                        <div class="grid-content bg-purple sum">
                          <i class="el-icon-download"></i>
                          <div class="weather-value ng-binding">8℃</div>
                          <div class="weathers-value">WEATHER</div>
                        </div>
                      </el-col>
                    </el-row>
                  </div>
                </el-col>
                <el-col :span="8" style="padding: 0 10px 0 0;" class="colle">
                  <div class="grid-content bg-purple">
                    <div class="rightTop">
                      <span class="clock-label">当地时间:</span>
                      <span class="clock-time">10:58 AM</span>
                    </div>
                    <div class="rightBottom">
                      <el-row type="flex" class="row-bg">
                        <el-col :span="12">
                          <div class="num">
                            <div class="radiuStyle">
                              <span class="radiu">NA</span>
                            </div>
                            <div class="weather-value ng-binding">8℃</div>
                            <div class="weathers-value">WEATHER</div>
                          </div>
                        </el-col>
                        <el-col :span="12">
                          <div class="num">
                            <div class="radiuStyle">
                              <span class="radiu">NA</span>
                            </div>
                            <div class="weather-value ng-binding">8℃</div>
                            <div class="weathers-value">WEATHER</div>
                          </div>
                        </el-col>
                      </el-row>
                    </div>
                  </div>
                </el-col>
              </el-row>
            </div>
          </div>  
      </div>
      <div class="nav">
        <el-tabs v-model="activeItem" @tab-click="handleClick" class="flight_tabs">
          <!-- <el-tab-pane label="General" name="General"></el-tab-pane> -->
          <el-tab-pane label="进港航班" name="Arrivals"></el-tab-pane>
          <el-tab-pane label="出港航班" name="Departures"></el-tab-pane>
          <el-tab-pane label="历史记录" name="HistoryMsg"></el-tab-pane>
          <!-- <el-tab-pane label="On Ground" name="Ground"></el-tab-pane>
          <el-tab-pane label="Reviews" name="Reviews"></el-tab-pane>
          <el-tab-pane label="Routes" name="Routes"></el-tab-pane>
          <el-tab-pane label="Weather" name="Weather"></el-tab-pane> -->
        </el-tabs>
        <keep-alive>
          <router-view name="Arrivals" v-if="activeItem === 'Arrivals'"></router-view>
        </keep-alive>
        <!-- <router-view name="General" v-if="activeItem === 'General'"></router-view> -->
        <router-view name="Departures" v-if="activeItem === 'Departures'"></router-view>
        <router-view name="HistoryMsg" v-if="activeItem === 'HistoryMsg'"></router-view>
        <!-- <router-view name="Ground" v-if="activeItem === 'Ground'"></router-view>
        <router-view name="Reviews" v-if="activeItem === 'Reviews'"></router-view>
        <router-view name="Routes" v-if="activeItem === 'Routes'"></router-view>
        <router-view name="Weather" v-if="activeItem === 'Weather'"></router-view> -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeItem: "Arrivals",
    }
  },

  watch: {
    $route() {
      this.activeItem = this.$route.params.item
    },
  },

  mounted() {
    this.activeItem = this.$route.params.item
  },
  
  methods: {
    handleClick(tab, event) {
      this.$router.push({ params: { item: tab.name } })
    },
  },
}
</script>
<style lang="less">
#app {
  text-rendering: optimizeSpeed;
  height: 100% !important;
  width: 100% !important;
  min-width: 320px;
  min-height: 264px;
  position: relative;
  margin: 0px;
  padding: 0px;
  font: normal 14px Helvetica Neue, Arial, sans-serif;
  color: #333;
  background: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;

  #cnt-data-content {
    width: 100%;
    max-width: 1150px;
    margin: auto;
    margin-top: 80px;
    height: 100%;
    background: #fff;

    .header {
      background: #393939;
      height: 60px !important;
      min-height: 190px;
      max-width: 1150px;
      margin: 0;

      .left {
        float: left;
        width: 50%;
        height: 100%;
        // background: #f8c023;

        h2 {
          margin-top: 50px;
        }

        h1,
        h2,
        h3 {
          margin-left: 40px;
          color: #fff;
        }
      }
      .right {
        float: right;
        width: 50%;
        height: 100%;

        .top {
          .col {
            text-align: center;
            margin-top: 50px !important;
          }

          .cnt-chart {
            height: 50px;
            border-radius: 50%;
            overflow: hidden;
            position: relative;

            .slice {
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
            }
          }
          #rating-overall {
            width: 50px;
            height: 50px;
            background: #6d6d6d;
            margin: 0 auto;

            .one {
              clip: rect(0 50px 25px 0);
              -webkit-transform: rotate(90deg);
              transform: rotate(90deg);
              background: #6d6d6d;
            }

            .two {
              clip: rect(0 25px 50px 0);
              -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
              background: #6d6d6d;
            }
          }

          .chart-center {
            top: 3px;
            left: 3px;
            width: 44px;
            height: 44px;
            background: #393939;
            position: absolute;
            border-radius: 50%;

            span {
              font-size: 20px;
              line-height: 44px;
              color: #6d6d6d;
              display: block;
              text-align: center;
            }
          }

          .chart-label {
            margin-top: 0.6em;
            text-align: center;
            color: #a4a1a1;
          }
          .charts-label {
            text-align: center;
            color: #a4a1a1;
          }

          .sum {
            text-align: center;
          }

          .el-icon-star-off:before {
            font-size: 55px;
            color: #fff;
          }
          .el-icon-download:before {
            font-size: 55px;
            color: #fff;
          }
          .el-icon-sort:before {
            font-size: 55px;
            color: #fff;
          }
          .weather-value {
            font-family: "Roboto Condensed";
            font-size: 16px;
            line-height: 10px;
            color: #fff;
            padding-top: 5px;
          }
          .weathers-value {
            font-family: "Roboto Condensed";
            font-size: 10px;
            line-height: 10px;
            color: #a4a1a1;
            padding-top: 8px;
          }

          .rightTop {
            .clock-label {
              font-family: "Roboto Condensed";
              font-size: 10px;
              color: #a4a1a1;
            }
            .clock-time {
              color: #ffffff;
              font-size: 20px;
            }
          }
          .num {
            text-align: center;
          }
          .radiuStyle {
            width: 44px;
            height: 44px;
            border: 2px solid #555454;
            border-radius: 50%;
            text-align: center;
            margin-left: 23px;
          }
          .radiu {
            display: block;
            font-size: 20px;
            line-height: 44px;
            color: #6d6d6d;
            text-align: center;
          }
          .rightBottom {
            margin-top: 18px;
          }
          .colle {
            margin: 15px 0 !important;
          }
        }
      }
    }

    .nav {
      background: #fff;

      .el-tabs--top {
        width: 100%;
        background: #a4a1a1;
      }

      .el-tabs__header {
        margin: 0;
      }
      .el-tabs__nav {
        width: 100%;
      }

      .flight_tabs {
        .el-tabs__item {
          width: 33.33%;
          text-align: center;
          padding: 0;
        }
      }

      .el-tabs__item:hover {
        background: #000;
        color: #f8c023;
      }
      .el-tabs__item.is-active {
        background: #000;
        color: #f8c023;
        border-bottom: 0;
        z-index: 1;
      }
      .el-tabs__active-bar {
        background: #a4a1a1;
      }
    }
  }
}
.cnt-airport-details {
  color: #a4a1a1;
}
</style>



