<template>
  <ul class="right_menu">
    <li>
      <i class="el-icon-setting" @click="showSetting"></i>
    </li>
    <li>
      <i class="el-icon-rank" @click="fullScreen"></i>
    </li>
    <li>
      <i class="el-icon-search"></i>
    </li>
    <li>
      <i class="el-icon-star-on"></i>
    </li>
    <li class="filter">
      <i class="el-icon-time"></i>
    </li>
    <li>
      <i class="el-icon-plus" @click="zoomOut"></i>
    </li>
    <li>
      <i class="el-icon-minus" @click="zommIn"></i>
    </li>
  </ul>
</template>

<script>
  export default {
    data() {
      return {

      }
    },
    methods: {
      zoomOut() {
        this.$emit("zoom", 'add')
      },
      zommIn() {
        this.$emit("zoom", 'sub')
      },
      showSetting() {
        this.$emit("changeSettingVisible")
      },
      fullScreen() {
        document.requestFullscreen()
      }
    },
  }
</script>

<style lang="less">
  .right_menu {
    position: absolute;
    top: 130px;
    right: 0px;
    width: 40px;
    z-index: 9999;

    li {
      width: 40px;
      height: 40px;
      background-color: rgba(0, 0, 0, .7);
      margin-bottom: 3px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;

      i {
        color: #fff;
        font-size: 24px;
        font-weight: bold;
      }

      .el-icon-rank {
        transform: rotate(45deg);
      }
    }

    .filter {
      margin-bottom: 45px;
    }
  }
</style>



