<template>
  <div id="app">
    <div id="content">
      <div class="section">
        <h3>休眠中的轮挡</h3>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="gearNumber" label="轮挡编号">
          </el-table-column>
          <el-table-column prop="factoryNumber" label="出厂编号">
          </el-table-column>
          <el-table-column prop="airport" label="所属机场">
          </el-table-column>
          <el-table-column prop="closingTime" label="关闭时间">
          </el-table-column>
          <el-table-column prop="latlng" label="经度/纬度">
          </el-table-column>
          <el-table-column prop="lengthTime" label="使用总时长">
          </el-table-column>
          <el-table-column prop="electricity" label="电量">
            <template slot-scope="scope">
              <div class="electricity">
                <div class="electricityBg"></div>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="AlarmNormal" label="报警/正常">
            <template slot-scope="scope">
              <i class="el-icon-error" v-show="alarmNormal === 1"></i>
              <i class="el-icon-success" v-show="alarmNormal === 0"></i>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          gearNumber: "1221",
          factoryNumber: "D3233",
          airport: "西安航空公司",
          closingTime: "2018-03-15",
          latlng: "20°",
          lengthTime: "30分钟",
          electricity: "",
          AlarmNormal: "正常",
        },
      ],
      alarmNormal: 0,
    }
  },
}
</script>
<style lang="less" scoped>
#app {
  background: #fff;
  width: 100%;

  #content {
    height: 100%;
    padding: 50px 30px;

    .section {
      width: 100%;
      height: 100%;

      h3 {
        color: #000000;
        display: block;
        font-family: "Roboto Condensed";
        font-size: 3.6em;
        font-weight: normal;
        line-height: 0.8em;
        margin-bottom: 10px;
      }
    }
  }
  .electricity {
    position: relative;
    width: 30px;
    height: 10px;
    background: #fff;
    border-radius: 10px;
    border: 1px solid #000;
  }
  .electricityBg {
    width: 20px;
    height: 100%;
    background: rgb(94, 92, 92);
    border-radius: 10px 0 0 10px;
  }
  .el-icon-error {
    color: red;
    font-size: 20px;
  }
  .el-icon-success {
    color: rgb(11, 245, 31);
    font-size: 20px;
  }
}
</style>




