<template>
  <div class="wrap_line" :style="{width: this.width + 'px'}">
    <div class="fill_line" :style="{width: this.nowRatios}"></div>
    <div class="drag_ball" :style="{left: this.nowRatios}" @mousedown="dragStart"  @mouseup="dragEnd"></div>
  </div>
</template>

<script>
  export default {
    props: {
      width: {
        type: Number,
        default: 126,
      },
      ratios: {
        type: Number,
        default: 0.5,
      },
    },
    data() {
      return {
        startX: '',
        endX: '',
        move: false,
        moved: false,
        copyRatios: this.ratios,
      }
    },

    computed: {
      dragLength() {
        return this.endX - this.startX
      },
      newRatios() {
        return this.copyRatios + this.dragLength / this.width
      },
      nowRatios() {
        if(this.moved) {
          return Math.floor(this.newRatios * 100) + "%"
        }else {
          return Math.floor(this.copyRatios * 100) + "%"
        }
      }
    },

    mounted() {
      document.addEventListener('mousemove', this.dragMove)
      document.addEventListener('mouseup', this.dragEnd)
    },

    beforeDestroy() {
      document.removeEventListener('mousemove', this.dragMove)
      document.removeEventListener('mouseup', this.dragEnd)
    },

    methods: {
      dragStart(evt) {
        this.move = true
        if(this.moved) {
          this.copyRatios = this.newRatios
        }

        this.startX = evt.clientX
        document.removeEventListener('mousemove', this.dragMove)
        document.addEventListener('mousemove', this.dragMove)
      },
      dragMove(evt) {
        if(!this.move) {
          return
        }

        if(this.moved && (this.newRatios <= 0.01 || this.newRatios >=0.99)) {
          console.log(122)
        }

        this.endX = evt.clientX

        if(!this.moved) {
          this.moved = true
        }
      },
      dragEnd(evt) {
        document.removeEventListener('mousemove', this.dragMove)
        this.move = false
      },
    }
  }
</script>

<style lang="less">
  .wrap_line {
    position: relative;
    width: 126px;
    height: 12px;
    border-radius: 6px;
    background: #d1d0d0;

    .fill_line {
      height: 12px;
      border-radius: 6px;
      background: #f8c023;
    }

    .drag_ball {
      position: absolute;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background: #fff;
      cursor: pointer;
    }
  }
</style>



