<template>
  <div id="tracker">
    <div id="content">
      <el-form :model="searchInfo" class="demo-form-inline search_form" label-position="top" ref="searchForm">
        <el-row :gutter="10">
          <el-col :span="4">
            <el-form-item label="编号" class="grid-content" prop="params.number">
              <el-input size="mini" v-model="searchInfo.params.number" placeholder="编号"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4">
            <el-form-item label="负责人" class="grid-content" prop="params.person">
              <el-input size="mini" v-model="searchInfo.params.person" placeholder="负责人"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4">
            <el-form-item label="联系电话" class="grid-content" prop="params.contact_tel">
              <el-input size="mini" v-model="searchInfo.params.contact_tel" placeholder="联系电话"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div class="btn_wrap">
        <el-row>
          <el-col :span="8" :offset="15">
            <div class="grid-content bg-purple" style="float: right; margin-bottom: 20px;">
              <el-button type="primary" icon="el-icon-search" size="mini" @click="searchHandler">查询</el-button>
              <el-button type="primary" icon="el-icon-plus" size="mini" @click="install">添加</el-button>
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="receive_list">
        <el-table :data="tableData" border style="width: 100%" size="small" 
        element-loading-background="rgba(0, 0, 0, 0.8)">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column fixed type="index" :index="indexMethod" label="序号"></el-table-column>
          <el-table-column sortable prop="number" label="编号"></el-table-column>
          <el-table-column sortable prop="person" label="负责人"></el-table-column>
          <el-table-column sortable prop="contact_tel" label="联系电话"></el-table-column>
          <el-table-column sortable prop="email" label="邮件"></el-table-column>
          <el-table-column sortable prop="department" label="归属部门"></el-table-column>
          <el-table-column sortable prop="purchasef_date" label="购买日期"></el-table-column>
          <el-table-column sortable prop="store_address" label="存放地址"></el-table-column>
          <el-table-column sortable prop="remarks" label="备注"></el-table-column>
        </el-table>
        <el-pagination
          v-if="total"
          @current-change="pageChange"
          :current-page="searchInfo.params.currPage"
          :page-size="10"
          layout="total, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
    <install-add :visible="installVisible" :title="title" @close="close" ></install-add> 
  </div>
</template>

<script>
  import InstallAdd from "./install.vue"
  export default {
    data() {
      return {
        title: '轮挡数据',
        installVisible: false,
        searchInfo: {
          params: {
            currPage: 1,
            number: '',
            person: '',
            contact_tel: '',
            email: '',
            department: '',
            purchasef_date: '',
            store_address: '',
            remarks: '',
          },
        },
        tableData: [
          {
            number: 1,
            person: '朵朵',
            contact_tel: '15878956452',
            email: '123456789@qq.com',
            department: 'lianxintongda',
            purchasef_date: '2018-03-26',
            store_address: '北京顺义',
            remarks: '轮挡数据',
          },
          {
            number: 2,
            person: '良良',
            contact_tel: '15878956250',
            email: '1234567890@qq.com',
            department: 'lianxintongda',
            purchasef_date: '2018-03-26',
            store_address: '北京顺义',
            remarks: '轮挡数据',
          }
        ],
        total: '',
      }
    },
    components: {
      InstallAdd
    },
    mounted() {
    },
    methods: {
      // 点击查询，页面为第一页
      searchHandler() {
      },
      install() {
        this.installVisible = true
      },
      indexMethod(index) {
        return (this.searchInfo.params.currPage - 1) * 10 + index + 1
      },
      close() {
        this.installVisible = false
      },
    }
  }
</script>
<style lang="less">
#tracker {
  text-rendering: optimizeSpeed;
  height: 100% !important;
  width: 100% !important;
  min-width: 320px;
  min-height: 264px;
  margin: 0px;
  padding: 0px;

  #content {
    width: 100%;
    max-width: 1150px;
    margin: auto;
    margin-top: 80px;
    height: 100%;
  }
}

.el-table {
  .cell {
    white-space: nowrap !important;
  }
}

</style>


