<template>
  <div id="app">
    <div id="content">
      <div class="section">
        <h3>出港航班</h3>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="date" label="到达时间/计划到达时间" width="180">
          </el-table-column>
          <el-table-column prop="flight_no" label="航班号" width="180">
          </el-table-column>
          <el-table-column prop="departure" label="始发站" width="180">
          </el-table-column>
          <el-table-column prop="aviation" label="航空公司">
          </el-table-column>
          <el-table-column prop="aircraft" label="飞机型号" width="180">
          </el-table-column>
          <el-table-column prop="latlng" label="经度/纬度" width="180">
          </el-table-column>
          <el-table-column prop="status" label="轮挡使用状态">
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [
        {
          date: "2016-05-02",
          flight_no: "JD3217",
          departure: "上海市",
          aviation: "西安航空公司",
          aircraft: "大型宽体飞机",
          latlng: "30°",
          status: "慢车",
        },
      ],
    }
  },
}
</script>
<style lang="less" scoped>
#app {
  background: #fff;
  width: 100%;

  #content {
    height: 100%;
    padding: 50px 30px;

    .section {
      width: 100%;
      height: 100%;

      h3 {
        color: #000000;
        display: block;
        font-family: "Roboto Condensed";
        font-size: 3.6em;
        font-weight: normal;
        line-height: 0.8em;
        margin-bottom: 10px;
      }
    }
  }
}
</style>




