<template>
  <div id="app">
    <div id="content">
      <div class="section">
        <h3>历史记录</h3>
        <div class="Search">
          <el-form :model="searchInfo" class="demo-form-inline search_form" label-position="top" ref="searchForm">
            <el-row :gutter="10">
              <el-col :span="4">
                <el-form-item label="始发站" class="grid-content" prop="params.departure">
                  <el-input size="mini" v-model="searchInfo.params.departure" placeholder="始发站" ref='departure'></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="4">
                <el-form-item label="航班号" class="grid-content" prop="params.flight_no">
                  <el-input size="mini" v-model="searchInfo.params.flight_no" placeholder="航班号"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
          <div class="btn_wrap">
            <el-row>
              <el-col :span="6" :offset="17">
                <div class="grid-content bg-purple" style="float: right">
                  <el-button type="primary" icon="el-icon-search" size="mini" @click="searchHandler">查询</el-button>
                  <el-button type="primary" icon="el-icon-refresh" size="mini" @click="reset">重置</el-button>
                </div>
              </el-col>
            </el-row>
          </div>
        </div>
        <div>
          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="date" label="日期">
            </el-table-column>
            <el-table-column prop="flight_no" label="航班号">
            </el-table-column>
            <el-table-column prop="departure" label="始发站">
            </el-table-column>
            <el-table-column prop="departion" label="目的站">
            </el-table-column>
            <el-table-column prop="lengthTime" label="使用时长">
            </el-table-column>
            <el-table-column prop="openTime" label="开启时间">
            </el-table-column>
            <el-table-column prop="closingTime" label="关闭时间">
            </el-table-column>
            <el-table-column prop="latlng" label="经度/纬度">
            </el-table-column>
            <el-table-column prop="status" label="状态">
              <template slot-scope="scope">
                <i class="el-icon-error" v-show="alarmNormal === 1"></i>
                <i class="el-icon-success" v-show="alarmNormal === 0"></i>
              </template>
            </el-table-column>
            <el-table-column  label="回放">
            <template slot-scope="scope">
              <img src="@/assets/record.png" alt="">
            </template>
          </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [
        {
          date: "2016-05-02",
          flight_no: "JD3217",
          departure: "上海市",
          departion: "西安",
          lengthTime: "1分钟",
          openTime: "30s",
          closingTime: "8s",
          latlng: "30°",
          status: "正常",
        },
      ],
      searchInfo: {
        params: {
          departure: "", //始发站
          flight_no: "", //航班号
        },
      },
      alarmNormal: 1,
    }
  },

  methods: {
    searchHandler() {},
    reset() {},
  },
}
</script>
<style lang="less" scoped>
#app {
  background: #fff;
  width: 100%;

  #content {
    height: 100%;
    padding: 50px 30px;

    .section {
      width: 100%;
      height: 100%;

      h3 {
        color: #000000;
        display: block;
        font-family: "Roboto Condensed";
        font-size: 3.6em;
        font-weight: normal;
        line-height: 0.8em;
        margin-bottom: 10px;
      }
      .grid-content.el-form-item {
        margin-top: 10px;
      }
    }
  }
  .el-icon-error {
    color: red;
    font-size: 20px;
  }
  .el-icon-success {
    color: rgb(11, 245, 31);
    font-size: 20px;
  }
}
</style>




